import React from 'react'
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import ExpandLessIcon from '@mui/icons-material/ExpandLess';
import MoreHorizIcon from '@mui/icons-material/MoreHoriz';
import MoodIcon from '@mui/icons-material/Mood';

const downIcon = <ExpandMoreIcon fontSize="small" />
const upIcon = <ExpandLessIcon fontSize="small" />
const noneIcon = <MoreHorizIcon fontSize="small" />


export type SuperSortPropsType = {
    id?: string
    sort: string
    value: string
    onChange: (newSort: string) => void
}

export const pureChange = (sort: string, down: string, up: string) => {
    //  , sort: (click) => down (click) => up (click) => '' (click) => down ...
    switch (sort) {
      case down: return up;    //    down  up
      case up: return '';      //  up  
      default: return down;    //     down
  }
}

const SuperSort: React.FC<SuperSortPropsType> = (
    {
        sort, value, onChange, id = 'hw15',
    }
) => {
    const up = '0' + value
    const down = '1' + value

    const onChangeCallback = () => {
        onChange(pureChange(sort, down, up))
    }

    const icon = sort === down
        ? downIcon
        : sort === up
            ? upIcon
            : noneIcon

    return (
        <span
            id={id + '-sort-' + value}
            onClick={onChangeCallback}
        >
            {/* */}
            {/* <img id={id + '-icon-' + sort} /> */}
            <MoodIcon fontSize="small" />
            {icon} 
        </span>
    )
}

export default SuperSort
